@import "../../_base.scss";

.videoHorizontal {
  border-bottom: 0.3px solid $border-color;
  cursor: pointer;

  &_left {
    position: relative;
    text-align: center;
    padding-left: 0 !important;
  }
  &_thumbnail {
    width: 100%;
    &-channel {
      width: 50%;
      border-radius: 50%;
    }

    &-wrapper {
      width: 100%;
    }
  }
  &_duration {
    position: absolute;
    bottom: 0.5rem;
    right: 1.2rem;
    padding: 0.2rem;
    background: #080808ec;
    border-radius: 3px;
    font-size: 0.9rem;
  }
  &_details {
    font-size: 0.9rem;
  }
  &_title {
    font-size: 1rem;
    //color: #fff;
    letter-spacing: 0.3px;
    @include line-clamp(2);
  }
  &_desc {
    @include line-clamp(2);
  }

  &_channel {
    img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      margin-right: 0.5rem;
      cursor: pointer;
    }
    p {
      font-size: 0.9rem;
      @include line-clamp(1);
    }
  }
}

@media (max-width: $breakpoint-small) {
  .videoHorizontal {
    font-size: 0.8rem;

    &_details {
      @include line-clamp(1);
    }
    &_channel {
      margin: 0.1rem 0;

      img {
        display: none;
      }
      p {
        font-size: 0.9rem;
      }
    }
    &_desc {
      display: none;
    }
    &_title {
      font-size: 14px;
      @include line-clamp(2);
    }
  }
}
