@import "../../_base.scss";

.video {
  margin-bottom: 1rem;
  padding: 0.7rem;

  font-weight: 500;
  font-size: 0.9rem;

  cursor: pointer;

  &_top {
    margin-bottom: 0.5rem;
    position: relative;

    .lazy-load-image-background {
      width: "100%";
    }
    img {
      width: 100%;
    }
    &_duration {
      position: absolute;
      bottom: 0.3rem;
      right: 0.3rem;

      padding: 0.2rem;
      background: #080808ec;
      border-radius: 3px;
    }
  }

  &_title {
    margin-bottom: 0.1rem;
    color: #fff;

    @include line-clamp(1);
  }
  &_details {
    display: flex;
    align-items: center;
  }
  &_channel {
    display: flex;
    align-items: center;
    margin: 0.5rem 0;

    img {
      width: 36px;
      height: 36px;

      border-radius: 50%;
      margin-right: 0.5rem;
      cursor: pointer;
    }
    p {
      margin-bottom: 0;
    }
  }
}
